import React, { useState, useEffect } from 'react';
import { Table, Button, Modal, Form, Input, Space, message } from 'antd';
import type { ColumnsType } from 'antd/es/table';

interface Department {
  id: number;
  name: string;
  code: string;
  description: string;
}

const App: React.FC = () => {
  const [departments, setDepartments] = useState<Department[]>([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isEditMode, setIsEditMode] = useState(false);
  const [currentDepartment, setCurrentDepartment] = useState<Department | null>(null);
  const [form] = Form.useForm();

  const fetchDepartments = () => {
    fetch('/api/departments')
      .then(res => res.json())
      .then(data => setDepartments(data))
      .catch(error => {
        console.error(':', error);
        message.error('');
      });
  };

  useEffect(() => {
    fetchDepartments();
  }, []);

  const columns: ColumnsType<Department> = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: '',
      dataIndex: 'description',
      key: 'description',
    },
    {
      title: '',
      key: 'action',
      render: (_, record) => (
        <Button type="link" onClick={() => handleEdit(record)}>
          
        </Button>
      ),
    },
  ];

  const handleEdit = (department: Department) => {
    setCurrentDepartment(department);
    setIsEditMode(true);
    form.setFieldsValue({
      name: department.name,
      code: department.code,
      description: department.description
    });
    setIsModalOpen(true);
  };

  const resetModal = () => {
    setIsModalOpen(false);
    setIsEditMode(false);
    setCurrentDepartment(null);
    form.resetFields();
  };

  const handleOk = () => {
    form.validateFields().then(values => {
      if (isEditMode && currentDepartment) {
        // 
        fetch(`/api/departments/${currentDepartment.id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(values),
        })
          .then(res => {
            if (!res.ok) {
              throw new Error('');
            }
            return res.json();
          })
          .then(data => {
            setDepartments(departments.map(dept => 
              dept.id === currentDepartment.id ? data : dept
            ));
            message.success('');
            resetModal();
          })
          .catch(error => {
            console.error(':', error);
            message.error('');
          });
      } else {
        // 
        fetch('/api/departments', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(values),
        })
          .then(res => {
            if (!res.ok) {
              throw new Error('');
            }
            return res.json();
          })
          .then(data => {
            setDepartments([...departments, data]);
            message.success('');
            resetModal();
          })
          .catch(error => {
            console.error(':', error);
            message.error('');
          });
      }
    });
  };

  return (
    <div style={{ padding: 24 }}>
      <Button 
        type="primary" 
        onClick={() => {
          setIsEditMode(false);
          setIsModalOpen(true);
        }}
        style={{ marginBottom: 16 }}
      >
        
      </Button>
      
      <Table 
        columns={columns} 
        dataSource={departments} 
        rowKey="id" 
      />

      <Modal 
        title={isEditMode ? "" : ""} 
        open={isModalOpen} 
        onOk={handleOk}
        onCancel={resetModal}
        okText={isEditMode ? "" : ""}
        cancelText=""
      >
        <Form form={form} layout="vertical">
          <Form.Item name="name" label="" rules={[{ required: true, message: '' }]}>
            <Input placeholder="" />
          </Form.Item>
          <Form.Item name="code" label="" rules={[{ required: true, message: '' }]}>
            <Input placeholder="" />
          </Form.Item>
          <Form.Item name="description" label="">
            <Input.TextArea placeholder="" />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default App;